import Vue from 'vue';
import Router from 'vue-router';
import Index from '../components/Index/Index.vue';
import Technology from '../components/Technology/Technology.vue';
import Study from '../components/Study/Study.vue';
import Exercise from '../components/Exercise/Exercise.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/technology',
      name: 'Technology',
      component: Technology
    },
    {
      path: '/study',
      name: 'Study',
      component: Study
    },
    {
      path: '/exercise',
      name: 'Exercise',
      component: Exercise
    },
  ]
})



